	border-radius: 10px;
	border-color: #f3f3f3;
}
.single-nav-thumb .slick-slide:hover img {
	border-color: #1a77f2;
}
.single-nav-thumb .slick-slide.slick-current.slick-active img {
	border-color: #ddd;
}
.single-nav-thumb .slick-arrow {
	top: 40%;
}

