input[type='checkbox'],
input[type='radio'] {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	padding: 0;
}

.switch {
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 64px;
	height: 30px;
	background-color: transparent;
	cursor: pointer;
}
.switch .switch-input {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	display: none;
	visibility: hidden;
}
.switch .switch-label {
	position: relative;
	display: block;
	height: inherit;
	font-size: 0.625rem;
	font-weight: 600;
	text-transform: uppercase;
	background-color: #fff;
	border: 1px solid #c2cfd6;
	border-radius: 4px;
	-webkit-transition: opacity background-color 0.3s 0.3s ease-out;
	transition: opacity background-color 0.3s 0.3s ease-out;
}
.switch .switch-handle {
	position: absolute;
	top: 3px;
	left: 3px;
	width: 24px;
	height: 24px;
	background: #fff;
	border: 1px solid #c2cfd6;
	border-radius: 1px;
	-webkit-transition: left 0.3s ease-out;
	transition: left 0.3s ease-out;
}

.switch .switch-input:checked ~ .switch-handle {
	left: 37px;
}

